* {
	margin: 0;
	padding: 0; }

body {
	padding: 139px 39px 1589px 34px;
	.top_layout {
		position: relative;
		height: 467px;
		background: linear-gradient(#eee, #fff, #eee);
		border-radius: 10px;
		::before {
			content: " ";
			position: absolute;
			top: -1px;
			left: -1px;
			width: 100%;
			height: 100%;
			padding: 1px;
			background: linear-gradient(#eee, #d2d2d2);
			z-index: -1;
			border-radius: 10px; }
		::after {
			content: "Описание";
			position: absolute;
			top: -5px;
			left: 16px;
			width: 200px;
			height: 20px;
			background: linear-gradient(#faa605, #d15100);
			z-index: 1;
			border-radius: 15px; } } }
